var THREE = require('three');
var STLLoader = require('three-stl-loader')(THREE);
var OrbitControls = require('three-orbitcontrols');
var STLViewer = /** @class */ (function () {
    function STLViewer() {
        this.addPopupTemplateHtmlToDom();
        this.handleEvents();
    }
    STLViewer.prototype.addPopupTemplateHtmlToDom = function () {
        document.body.innerHTML +=
            '<div id="stlBackdrop">' +
                '  <div id="stlContent">' +
                '    <div id="stlClose">' +
                '      <i class="fas fa-times"></i>' +
                '    </div>' +
                '    <div id="stlModel">' +
                '    </div>' +
                '  </div>' +
                '</div>';
    };
    STLViewer.prototype.init = function (filePath, color, width, height) {
        this.initScene();
        this.initCamera(width, height);
        this.scene.add(this.camera);
        var grid = new THREE.GridHelper(10, 50, 0x96CBDE, 0xA6DBEF);
        grid.rotateOnAxis(new THREE.Vector3(1, 0, 0), 90 * (Math.PI / 180));
        this.scene.add(grid);
        this.renderer = new THREE.WebGLRenderer({ antialias: true });
        this.renderer.setClearColor(0xFAFAFA);
        this.renderer.setPixelRatio(window.devicePixelRatio);
        this.renderer.setSize(width, height);
        document.getElementById("stlModel").appendChild(this.renderer.domElement);
        var loader = new STLLoader();
        // Binary files
        var material = new THREE.MeshPhongMaterial({ color: color, specular: 0x0, shininess: 50 });
        var parent = this;
        loader.load(filePath, function (geometry) {
            parent.scene.add(parent.getMesh(geometry, material));
            parent.render();
        });
        this.initControls();
        window.addEventListener('resize', this.onWindowResize, false);
    };
    STLViewer.prototype.getMesh = function (geometry, material) {
        var mesh = new THREE.Mesh(geometry, material);
        mesh.position.set(0, 0, 0);
        mesh.rotation.set(0, 0, 0);
        mesh.scale.set(.1, .1, .1);
        mesh.castShadow = true;
        mesh.receiveShadow = true;
        return mesh;
    };
    STLViewer.prototype.initCamera = function (width, height) {
        this.camera = new THREE.PerspectiveCamera(35, width / height, 1, 500);
        // Z is up for objects intended to be 3D printed.
        this.camera.up.set(0, 0, 1);
        this.camera.position.set(0, -9, 6);
        this.camera.add(new THREE.PointLight(0xffffff, 0.8));
    };
    STLViewer.prototype.initScene = function () {
        this.scene = new THREE.Scene();
        this.scene.add(new THREE.AmbientLight(0x999999));
    };
    STLViewer.prototype.initControls = function () {
        var _this = this;
        var controls = new OrbitControls(this.camera, this.renderer.domElement);
        controls.addEventListener('change', function () {
            _this.render();
        });
        controls.target.set(0, 0, 0);
        controls.update();
    };
    STLViewer.prototype.onWindowResize = function () {
        this.camera.aspect = window.innerWidth / window.innerHeight;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        this.render();
    };
    STLViewer.prototype.render = function () {
        this.renderer.render(this.scene, this.camera);
    };
    STLViewer.prototype.handleEvents = function () {
        var backdrop = $("#stlBackdrop");
        var model = $("#stlModel");
        var closeBtn = $("#stlClose");
        var viewStlFileBtn = $(".viewStlFile");
        var parent = this;
        viewStlFileBtn.on("click", function () {
            model.html("");
            var filePath = $(this).data("file");
            var color = $(this).data("color");
            parent.init(filePath, Number(color), 800, 600);
            backdrop.fadeIn();
        });
        $(document).on('keyup', function (e) {
            if (e.keyCode === 27) { // escape key maps to keycode `27`
                backdrop.fadeOut(function () {
                    model.html("");
                });
            }
        });
        closeBtn.on("click", function (e) {
            backdrop.fadeOut(function () {
                model.html("");
            });
        });
        backdrop.on("click", function (e) {
            if (e.target !== this)
                return;
            $(this).fadeOut(function () {
                model.html("");
            });
        });
    };
    return STLViewer;
}());
export default STLViewer;
